	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/link-the-dom-monster/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/portfolio-2018/static/dom-monster-1ac668ca3c8752df6db369fc2abd9845-41a8d.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 55.99999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"The DOM Monster!\"\n        title=\"\"\n        src=\"/portfolio-2018/static/dom-monster-1ac668ca3c8752df6db369fc2abd9845-41a8d.png\"\n        srcset=\"/portfolio-2018/static/dom-monster-1ac668ca3c8752df6db369fc2abd9845-bf474.png 158w,\n/portfolio-2018/static/dom-monster-1ac668ca3c8752df6db369fc2abd9845-d5dd3.png 315w,\n/portfolio-2018/static/dom-monster-1ac668ca3c8752df6db369fc2abd9845-41a8d.png 500w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>I'm always on the look out for new yardsticks to measure by code by, and here's another one that I've been introduced to recently. It's called [the DOM Monster](<a href=\"http://mir.aculo.us/dom-monster/\">http://mir.aculo.us/dom-monster/</a> \"The DOM Monster\"\"), and it's something that you can drag and drop into your browser bookmarks bar, and then later click on to evaluate the current HTML page, based on things like its content, complexity, nesting levels and general content optimization.</p>\n<p>The monster gives you a report and a summary of what it's found, and it's generally a pretty good summary of best practices for your HTML and JavaScript construction, even if it's not linking you specifically to the original rules that govern this sort of thing.  All of which will improve your pages' rendering speed, performance on older machines, and performance on mobile devices, which is what a lot of people care about nowadays.</p>","frontmatter":{"title":"Link: The DOM Monster","date":"March 24, 2011","tags":["javascript"],"url":null}}},"pathContext":{}}

